<!DOCTYPE html>

<template>
  <html>
  <head>
    <title>Bootstrap Example</title>
    <meta charset="utf-8">
    <meta name="viewport" content="width=device-width, initial-scale=1">
    <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.4.0/css/bootstrap.min.css">
    <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.0/css/all.css"
          integrity="sha384-Mmxa0mLqhmOeaE8vgOSbKacftZcsNYDjQzuCOm6D02luYSzBG8vpaOykv9lFQ51Y" crossorigin="anonymous">
  </head>
  <body>

  <div>
    <b-navbar class="navbar navbar-expand-sm navbar-dark bg-dark">
      <b-container>
        <b-navbar-nav>
          <b-nav-item @click="solve()">Start</b-nav-item>

          <!-- Navbar dropdowns -->
          <b-nav-item-dropdown text="Size">
            <b-dropdown-item @click="setSize(1)">30x50</b-dropdown-item>
            <b-dropdown-item @click="setSize(2)">40x66</b-dropdown-item>
            <b-dropdown-item @click="setSize(3)">50x83</b-dropdown-item>
            <b-dropdown-item @click="setSize(4)">60x100</b-dropdown-item>
          </b-nav-item-dropdown>

          <b-nav-item-dropdown @click="clearGrid()" text="Algorithm">
            <b-dropdown-item @click="clearGrid()">Breadthfirst</b-dropdown-item>
            <b-dropdown-item>Depthfirst</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-container>
    </b-navbar>
  </div>


  <div>
    <div id="grid-container"></div>
  </div>

  </body>
  </html>
</template>

<script>
  import $ from 'jquery'

  let isDrawing = false;
  let rows = 30;
  let cols = 50;

  //Prepare Components
  function createGrid(rows, cols) {
    const container = document.getElementById("grid-container");
    container.style.setProperty('--grid-rows', rows);
    container.style.setProperty('--grid-cols', cols);
    for (var i = 0; i < (rows * cols); i++) {
      let cell = document.createElement("div");
      container.appendChild(cell).className = "grid-item";
      cell.id = "cell-" + i;
      cell.ondragstart = "return false;";
      cell.ondrop = "return false;";
    }
  }

  function clearGrid() {
    for (var i = 0; i < (rows * cols); i++) {
      let cell = document.getElementById("cell-" + i);
      if (cell.style.backgroundColor !== "black") {
        console.log(1);
        cell.style.animation = 'none';
        cell.offsetHeight; /* trigger reflow */
        cell.style.animation = null;
        cell.style.backgroundColor = "white";
      }
    }
  }

  function drawWall(id) {
    let cell = document.getElementById(id);
    cell.style.animation = 'none';
    cell.offsetHeight; /* trigger reflow */
    cell.style.animation = null;
    cell.style.backgroundColor = "black";
  }

  function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  function getNodeIndex(row, col) {
    if (row >= 0 && col >= 0 && row < rows && col < cols) {
      var index = (row * cols) + col;
      return index;
    }
    return null;
  }

  function getAdjNode(position) {
    let index = getNodeIndex(position[0], position[1]);
    if (position[0] < 0 || position[0] >= rows || position[1] < 0 || position[1] >= cols) {
      return null;
    } else if (document.getElementById("cell-" + index).style.backgroundColor == "black") {
      return null;
    } else {
      return position;
    }
  }

  function createGridGraph() {
    var gridGraph = [];
    for (var i = 0; i < rows; i++) {
      for (var j = 0; j < cols; j++) {
        var right = getAdjNode([i, j + 1]);
        var left = getAdjNode([i, j - 1]);
        var up = getAdjNode([i - 1, j]);
        var down = getAdjNode([i + 1, j]);
        var node = {value: [i, j], left: left, right: right, up: up, down: down};
        gridGraph.push(node);
      }
    }
    return gridGraph;
  }

  function bfs() {
    let visited = [];
    let gridGraph = createGridGraph();
    let queue = [];
    let visitOrder = [];
    let path = [];
    let targetIndex = getNodeIndex(20, 20);
    let startIndex = getNodeIndex(0, 0);
    let node = gridGraph[startIndex];
    let targetNode = gridGraph[targetIndex];
    visited [startIndex] = true;
    path.push(node);
    queue.push(path);

    while (queue.length !== 0) {
      path = queue.shift();
      node = path[path.length - 1];
      visitOrder.push(node);

      if (node === targetNode) {
        return [path, visitOrder];
      }

      if (node.left && !visited[getNodeIndex(node.left[0], node.left[1])]) {
        let leftIndex = getNodeIndex(node.left[0], node.left[1]);
        let pathToNextNode = path.slice();
        pathToNextNode.push(gridGraph[leftIndex]);
        queue.push(pathToNextNode);
        visited[leftIndex] = true;
      }
      if (node.right && !visited[getNodeIndex(node.right[0], node.right[1])]) {
        let rightIndex = getNodeIndex(node.right[0], node.right[1]);
        let pathToNextNode = path.slice();
        pathToNextNode.push(gridGraph[rightIndex]);
        queue.push(pathToNextNode);
        visited[rightIndex] = true;
      }
      if (node.up && !visited[getNodeIndex(node.up[0], node.up[1])]) {
        let upIndex = getNodeIndex(node.up[0], node.up[1]);
        let pathToNextNode = path.slice();
        pathToNextNode.push(gridGraph[upIndex]);
        queue.push(pathToNextNode);
        visited[upIndex] = true;
      }
      if (node.down && !visited[getNodeIndex(node.down[0], node.down[1])]) {
        let downIndex = getNodeIndex(node.down[0], node.down[1]);
        let pathToNextNode = path.slice();
        pathToNextNode.push(gridGraph[downIndex]);
        queue.push(pathToNextNode);
        visited[downIndex] = true;
      }
    }
  }

  //Wait fo document to create initial grid
  $(document).ready(function () {
    createGrid(rows, cols);
  });

  // Add mouse listeners to draw
  $(document).on('mousedown mousemove mouseup dragover dragleave', '.grid-item', function (e) {
    if (e.type == "mousedown" || e.type == "dragover") {
      isDrawing = true;
      drawWall(e.target.id);
    } else if (e.type == "mousemove" && isDrawing) {
      drawWall(e.target.id);
    } else if (e.type == "mouseup" || e.type == "dragleave") {
      isDrawing = false;
    }
  });

  export default {
    name: 'home',
    data() {
      return {
        msg: 'Example'
      }
    },
    created() {


    },
    methods: {
      solve: async function () {
        clearGrid();
        let pathAndVisitOrder = bfs();
        let visitedOrder = pathAndVisitOrder[1];
        let path = pathAndVisitOrder[0];
        console.log(visitedOrder);
        for (let i = 0; i < visitedOrder.length; i++) {
          let node = visitedOrder[i];
          let nodeIndex = getNodeIndex(node.value[0], node.value[1]);
          let cell = document.getElementById("cell-" + nodeIndex);
          cell.style.animation = 'none';
          cell.offsetHeight; /* trigger reflow */
          cell.style.animation = null;
          await sleep(5);
          cell.style.animation = "mymove 5s";
          cell.style.animationFillMode = "both";
        }
        for (let i = 0; i < path.length; i++) {
          let node = path[i];
          let nodeIndex = getNodeIndex(node.value[0], node.value[1]);
          let cell = document.getElementById("cell-" + nodeIndex);
          cell.style.animation = 'none';
          cell.offsetHeight; /* trigger reflow */
          cell.style.animation = null;
          await sleep(5);
          cell.style.animation = "mymove-1 4s";
          cell.style.animationFillMode = "both";
        }
      },
      setSize: function (size) {
        //comment
        const container = document.getElementById("grid-container");
        container.innerHTML = '';
        switch (size) {
          case 1:
            rows = 30;
            cols = 50;
            break;
          case 2:
            rows = 40;
            cols = 66;
            break;
          case 3:
            rows = 50;
            cols = 83;
            break;
          case 4:
            rows = 60;
            cols = 100;
            break;
          default:
            break;
        }
        createGrid(rows, cols);

      }
    }
  }

</script>

<style>

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  :root {
    --grid-cols: 1;
    --grid-rows: 1;
  }

  #grid-container {
    display: grid;
    grid-gap: 0cm;
    border-left: 1px solid black;
    border-top: 1px solid black;
    grid-template-rows: repeat(var(--grid-rows), 1fr);
    grid-template-columns: repeat(var(--grid-cols), 1fr);
    margin-bottom: 10px;
    margin-right: 10px;
    margin-left: 10px;
    /*width: 100%;*/
    alignment: center;
    /*background-color: blue;*/
  }

  .grid-item {
    width: 100%;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    text-align: center;
  }

  .grid-item:after {
    content: "";
    display: block;
    padding-bottom: 100%;
  }

  #img-container:hover img {
    opacity: 0.8;
  }

  .navbar {
    color: #42b983;
    height: 10%;
    padding-top: 0px !important;
    padding-bottom: 0px !important;

  }

  .navbar-nav > li > a {
    /*font-size: 10 !important;*/
    padding-top: 10px !important;
    padding-bottom: 10px !important;
  }

  .navbar-nav {
    font-size: 10 !important;
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }

  .navbar {
    min-height: 20px !important
  }

  @keyframes mymove {
    from {
      background-color: yellow;
    }
    to {
      background-color: blue;
    }
  }

  @keyframes mymove-1 {
    from {
      background-color: yellow;
    }
    to {
      background-color: purple;
    }
  }


</style>



